<template>
    <div class="tab flex">
      <div class="tabLeft"></div>
      <div class="tabCn flex">
            <div class="text flex">
              <p class="textCn"  @click="changePage(0)">home</p>
              <span class="icon" v-show="hasPhoto > 0">></span>
              <p class="textCn" @click="changePage(1)" v-show="hasPhoto > 0">Pathologic Analysis</p>
              <span class="icon" v-show="hasPhoto > 1">></span>
              <p class="textCn" @click="changePage(2)" v-show="hasPhoto > 1">Prognostic Analysis</p>
            </div>
            <div class="tabrt flex">
                <el-menu default-active="1" class="language" mode="horizontal" @select="handleSelect" background-color="#3075B1" text-color="#fff" active-text-color="#fff">
                  <el-submenu index="1">
                    <template slot="title">{{language}}</template>
                    <el-menu-item index="english">ENGLISH</el-menu-item>
                     <el-menu-item index="chinese">简体中文</el-menu-item>
                  </el-submenu>
                </el-menu>
                <el-menu default-active="2" class="user" mode="horizontal" @select="sltLogin" background-color="#3075B1" text-color="#fff" active-text-color="#fff">
                  <el-submenu index="2">
                    <template slot="title">
                      <i class="el-icon-s-custom" style="color:#fff;"></i>
                      <span>admin</span>
                    </template>
                    <el-menu-item index="exitLogin">logout</el-menu-item>
                  </el-submenu>
                </el-menu>
            </div>
      </div>
      <div class="tabRight"></div>
    </div>
</template>

<script>
export default {
    name:"v-Tab",
    props:{
      hasPhoto:Number
    },
  data () {
    return {
      language:"ENGLISH"
    }
  },
  methods: {
    handleSelect(key) {
      if(key==="english"){
        this.language="ENGLISH"
      }else if(key==="chinese"){
        this.language="简体中文"
      }
    },
    sltLogin(key){
      if(key==="exitLogin"){
        this.$router.push({ path:'/login'})
      }
    },
    changePage(val){
      this.$emit("changePage",val);
    }
  }
}
</script>

<style scoped>
    .flex{
    display:flex;
    align-items:center;
    }
  .tab{
   width:100%;
   height:60px;
   background:#6ea3d7;
  }
  .tabLeft{
    flex:1;
    height:60px;
    background:url("../assets/tabLeft.png");
    background-size:1px 60px;
    background-repeat:repeat-x;
  }
  .tabRight{
    flex:1;
    height:60px;
    background:url("../assets/tabRight.png");
    background-size:1px 60px;
    background-repeat:repeat-x;
  }
  .tabCn{
    width:90%;
    min-width:1200px;
    height:60px;
    background:url("../assets/tab.png");
    background-size:100% 100%;
  }
  .tabCn .text{
      flex:1;
      font-size:16px;
      color:#fff;
      line-height:60px;
  }
  .tabCn .text .textCn{
    cursor:pointer;
  }
  .tabCn .text .icon{
    margin:0 5px;
  }
  .tabCn .language{
      margin-right:24px;
      cursor:pointer;
  }
 .tabCn .language p,.tabCn .user p{
     font-size:14px;
      color:#fff;
      margin:0 4px;
 }
 .tabCn .language img{
     width:15px;
     height:15px;
 }
 .tabCn .user{
   cursor:pointer;
 }
 .tabCn .user .img1{
     width:26px;
     height:26px;
 }
 .tabCn .user .img2{
     width:15px;
     height:15px;
 }
 .tabrt{
     padding-right:50px;
 }
</style>



