<template>
<div class="main">
  <v-Header></v-Header>
  <v-Tab :hasPhoto="hasPhoto" @changePage="changePage"></v-Tab>
  <div class="home">
    <!-- 上传图片界面 -->
    <div class="content" v-show="hasPhoto==0">
      <div class="upImg">
        <el-upload
        class=""
        action="http://dev.ankon-hospital.com:8888/detect"
        name="img"
        list-type="text"
        :show-file-list="false"
        :on-progress="upPro"
        :on-success="handleAvatarSuccess"
        :on-error="upError"
        :on-change="imgPreview"
        :before-upload="beforeAvatarUpload">
        <p>Click to upload pathological image</p>
        </el-upload>
      </div>
      <div class="tips">
        <p>Note:</p>
        <p>1.Please upload digital hematoxylin and eosin stained pathological images</p>
        <p>2.Supported format: PNG/JPG/JPEG/BMP (suggested file size not to exceed 5mb).</p>
      </div>
    </div>

    <!-- 生成热力图的界面 -->
    <div class="photo" v-show="hasPhoto===1">
      <div class="picLeft">
        <p class="tit">Original Image</p>
        <div class="picLeftImg">
          <img :src="imageUrl" />
        </div>
        <div class="bottom">
          <p class="text">{{imageName}}</p>
          <div class="button">
            <el-upload
            class=""
            action="http://dev.ankon-hospital.com:8888/detect"
            name="img"
            list-type="text"
            :show-file-list="false"
            :on-progress="upPro"
            :on-success="handleAvatarSuccess"
            :on-error="upError"
            :on-change="imgPreview"
            :before-upload="beforeAvatarUpload">
            <p>Upload Image</p>
            </el-upload>
          </div>
        </div>
      </div>
      <div class="picRight">
        <p class="tit">Heatmap</p>
        <div class="picRightImg">
          <img :src="baseUrl" />
        </div>
        <div class="bottom">
          <p class="text">Result:{{result}}</p>
          <div class="button" @click="getScore">Prognostic AI Predicting Socre</div>
        </div>
      </div>
    </div>

    <!-- 预后分析的界面 analysis-->
    <div class="photo" v-show="hasPhoto===2">
      <div class="picLeft">
        <p class="tit">Heatmap</p>
        <div class="picLeftImg">
          <img :src="baseUrl" />
        </div>
        <div class="bottom">
          <p class="text">Result:{{result}}</p>
        </div>
      </div>
      <div class="analysis">
        <div class="analysis-list flex">
          <p>Age:</p>
          <el-select v-model="ageVal" placeholder="" size="small">
            <el-option
              v-for="item in ageList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      <div class="analysis-list flex">
          <p>Gender:</p>
          <el-select v-model="genderVal" placeholder="" size="small">
            <el-option
              v-for="item in genderList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-list flex">
          <p>Longest Dimension:</p>
          <el-select v-model="ldVal" placeholder="" size="small">
            <el-option
              v-for="item in ldList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-list flex">
          <p>Neoplasm Histologic Grade:</p>
          <el-select v-model="nhgVal" placeholder="" size="small">
            <el-option
              v-for="item in nhgList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-list flex">
          <p>Pathologic Stage (8th):</p>
          <el-select v-model="psVal" placeholder="" size="small">
            <el-option
              v-for="item in psList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-list flex">
          <p>Pathologic Stage_T:</p>
          <el-select v-model="pstVal" placeholder="" size="small">
            <el-option
              v-for="item in pstList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-list flex">
          <p>Pathologic Stage_N:</p>
          <el-select v-model="psnVal" placeholder="" size="small">
            <el-option
              v-for="item in psnList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-list flex">
          <p>Pathologic Stage_M:</p>
          <el-select v-model="psmVal" placeholder="" size="small">
            <el-option
              v-for="item in psmList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="analysis-bottom" @click="analysis">Start analyzing</div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import vHeader from "./header.vue"
import vTab from "./tab.vue"
import  { Loading }  from 'element-ui';
let loading;
export default {
  data () {
    return {
        imageUrl: '',//原始图片地址
        imageName:'',//原始图片名称
        load:'',
        hasPhoto:0,
        baseUrl:'',
        result:'',
        ageList:[{value:0,label:">65"},{value:1,label:"<=65"},{value:2,label:"Missing"}],
        ageVal:0,
        genderList:[{value:0,label:"Female"},{value:1,label:"Male"},{value:2,label:"Missing"}],
        genderVal:0,
        ldList:[{value:0,label:"<=2mm"},{value:1,label:">2mm&&<=5mm"},{value:2,label:">5mm"},{value:3,label:"Missing"}],
        ldVal:0,
        nhgList:[{value:0,label:"G1"},{value:1,label:"G2"},{value:2,label:"G3"},{value:3,label:"G4"},{value:4,label:"Missing"}],
        nhgVal:0,
        psList:[{value:0,label:"1"},{value:1,label:"2"},{value:2,label:"3"},{value:3,label:"4"},{value:4,label:"Missing"}],
        psVal:0,
        pstList:[{value:0,label:"T1"},{value:1,label:"T2"},{value:2,label:"T3"},{value:3,label:"T4"},{value:4,label:"Missing"}],
        pstVal:0,
        psnList:[{value:0,label:"N0"},{value:1,label:"N1"},{value:2,label:"N2"},{value:3,label:"N3"},{value:4,label:"Missing"}],
        psnVal:0,
        psmList:[{value:0,label:"M0"},{value:1,label:"M1"},{value:2,label:"Missing"}],
        psmVal:0
    }
  },
  components:{
    vHeader,
    vTab
  },
  methods: {
       handleAvatarSuccess(res, file){//上传成功
        this.endLoading();
        this.hasPhoto=1;
        this.result=res.Prediction[0];
        this.baseUrl="data:image/png;base64,"+res['Heatmap(b64code)'];
      },
      beforeAvatarUpload(file) {//上传之前校验
        this.baseUrl=""
        const isJPG = (file.type === 'image/jpeg' || file.type === 'image/jpg'|| file.type === 'image/png'|| file.type === 'image/bmp');
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error("Supported format: PNG/JPG/JPEG/BMP");
        }
        if (!isLt2M) {
          this.$message.error('suggested file size not to exceed 5mb');
        }
        return isJPG && isLt2M;
      },
      upError(){//上传失败
        let that=this;
        setTimeout(function(){
          that.$message.error('Failed to analyze');
          that.endLoading();
        },2000)
      },
      upPro(){//上传中
        this.startLoading();
      },
      startLoading(){//开始加载中
        loading = Loading.service({
          lock: true,
          text: 'Upload Complete, Now Analyzing...Please Wait',
          background: 'rgba(0, 0, 0, 0.5)'
        });
      },
      endLoading(){//结束加载中
        loading.close();
      },
      imgPreview(file){//上传图片
        this.imageUrl = URL.createObjectURL(file.raw);
        this.imageName=file.name;
        this.hasPhoto=1
      },
      getScore(){//获取评分的界面
        this.hasPhoto=2
      },
      changePage(val){
        this.hasPhoto=val;
      },
      analysis(){//开始分析

      }
    }
}
</script>

<style scoped>
  .flex{
    display:flex;
    align-items:center;
    justify-content: space-between;
  }
  .content{
    width:90%;
    min-width:1200px;
    margin-bottom:5%;
  }
  .upImg{
    width:600px;
    height:44px;
    background:#fff;
    border-radius:44px;
    margin:0 auto 40px auto;
    position:relative;
    
  }
  .upImg p{
    line-height:44px;
    text-indent:20px;
    font-size:14px;
  }
  .tips{
    width:600px;
    margin:0 auto;
  }
  .tips p{
    font-size:16px;
    color:#fff;
    line-height:30px;
  }
   .photo{
        width:90%;
        min-width:1200px;
        display:flex;
        align-items:center;
        /* justify-content:space-between; */
    }
    .picLeft{
      margin-right:40px;
    }
    .picLeft,.picRight{
        width:40%;

    }
    .photo .analysis{
      background:#fff;
      width:40%;
      padding:5px 0;
    }
    .picLeft .tit,.picRight .tit{
        font-size:16px;
        color:#fff;
        margin-bottom:10px;
    }
    .picLeftImg,.picRightImg{
        width:100%;
        height:auto;
        overflow:hidden;
        border:1px solid #fff;
        min-height:300px;
    }
    .picLeftImg img,.picRightImg img{
        width:100%;
        height:auto;
        display:block;
    }
    .photo .bottom{
        width:100%;
        display:flex;
        align-items:center;
        justify-content: space-between;
        margin-top:20px;
    }
    .photo .bottom .text{
        font-size:14px;
        color:#fff;
    }
    .photo .bottom .button{
        font-size:14px;
        padding:0 20px;
        height:30px;
        line-height:30px;
        border-radius:30px;
        color:#1989FA;
        background:#fff;
        cursor:pointer;
    }
    .analysis-list{
      padding:5px 40px;
    }
    .analysis-bottom{
      background:#1989FA;
      height:40px;
      text-align:center;
      line-height:40px;
      color:#fff;
      margin:15px 40px;
      cursor:pointer;
    }
</style>



