<template>
    <div class="header">
      <div class="headerLeft"></div>
      <div class="headerCn">
        <div class="left">
          <img src="../assets/logo.png" class="logo" />
          <img src="../assets/hospitalName1.png" class="hospitalName" />
          <img src="../assets/hospitalName2.png" class="hospitalName" />
        </div>
        <div class="right" v-if="showRight">
          <p>diagnosis and survival prediction of gastric cancer</p>
        </div>
      </div>
      <div class="headerRight"></div>
    </div>
</template>

<script>
export default {
    name:"v-Header",
    props:{
      showRight:{
        type:Boolean,
        default:true
      }
    },
  data () {
    return {
      
    }
  }
  
}
</script>

<style scoped>
  .header{
    width:100%;
    height:93px;
    background:#6ea3d7;
    display:flex;
  }
  .headerLeft{
    flex:1;
    height:93px;
    background:url("../assets/headBgLeft.png");
    background-size:1px 93px;
    background-repeat:repeat-x;
  }
  .headerRight{
    flex:1;
    height:93px;
    background:url("../assets/headBgRight.png");
    background-size:1px 93px;
    background-repeat:repeat-x;
  }
  .header{
    width:100%;
    height:93px;
    
  }
  .headerCn{
    width:90%;
    min-width:1200px;
    height:93px;
    margin:0 auto;
    background:url("../assets/head.png");
    background-size:100% 100%;
    display:flex;
  }
  .headerCn .left{
    flex:1;
    display:flex;
    align-items:center;
  }
  .headerCn .right{
    font-size:26px;
    color:#fff;
    padding-right:50px;
    line-height:93px;
  }
  .logo{
    width:76px;
    height:76px;
  }
  .hospitalName{
    width:200px;
    margin-left:20px;
  }
</style>



