import Vue from 'vue'
import Router from 'vue-router'
import upImg from '@/components/upImg'
import login from '@/components/login'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/upImg',
      name: 'upImg',
      component: upImg
    }
  ]
})



