<template>
  <div class="main">
    <v-Header :showRight="showRight"></v-Header>
    <div class="login">
        <div class="loginCn">
             <p class="title">diagnosis and survival prediction of gastric cancer</p>
             <div class="loginForm">
                 <div class="list flex">
                     <div class="lf flex">
                         <img src="../assets/name.png"/>
                     </div>
                     <div class="rt">
                         <input type="text" placeholder="user name" v-model="userName">
                     </div>
                 </div>
                 <div class="list flex">
                     <div class="lf flex">
                         <img src="../assets/password.png"/>
                     </div>
                     <div class="rt">
                         <input type="password" placeholder="password" v-model="password">
                     </div>
                 </div>
                 <div class="login-btn" @click="login">login</div>
             </div>
        </div>
    </div>
  </div>
</template>

<script>
import vHeader from "./header.vue"
export default {
  data () {
    return {
       showRight:false,
       userName:"",
       password:""
    }
  },
  components:{
    vHeader
  },
  methods: {
      login(){
            if(this.userName.trim()==""){
                this.$message.error("Please enter the user name");
                return;
            }
            if(this.password.trim()==""){
                this.$message.error("Please enter your password");
                return;
            }
            if(this.userName.trim()==="admin" && this.password.trim()==="Wuhan2020"){
                this.$message({
                    message: 'Login successful',
                    type: 'success'
                });
                this.$router.push({ path:'/upImg'})
            }else{
                this.$message.error("Incorrect user name or password");
            }
      }
    }
}
</script>
e
<style scoped>
    .flex{
        display:flex;
        align-items:center;
    }
  .login{
    width:100%;
    height:calc(100% - 93px);
    background:#6ea3d7 url('../assets/login.png');
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    overflow: hidden;
    min-width:1200px;
  }
  .loginCn{
      width:500px;
      margin:7% auto 0 auto;
  }
  .loginCn .title{
      font-size:20px;
      color:#fff;
      text-align:center;
      line-height:50px;
      margin-bottom:20px;
  }
  .loginForm{
      background:#fff;
      border-radius:10px;
      padding:40px;
  }
   .list{
       margin-bottom:20px;
   }
  .list .lf{
      width:40px;
      height:40px;
      justify-content: center;
      background:#1989FA;
  }
  .list .lf img{
      width:16px;
      height:16px;
  }
    .list .rt{
        flex:1;
        height:40px;
        background:#F7F7F7;
    }
    .list .rt input{
        width:100%;
        height:40px;
        border:0;
        background:#F7F7F7;
        text-indent:20px;
        font-size:14px;
    }
    .login-btn{
        height:36px;
         background:#1989FA;
         text-align:center;
         line-height:36px;
         font-size:16px;
         color:#fff;
         cursor:pointer;
    }
</style>



